body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input {
  height: 30px;
  border-radius: 4px;
  display: inline-block;
  font-size: 16px;
  text-align: right;
  width: 100px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  position: relative;
  background-color: #282c34;
  min-height: 100vh;
  font-size: calc(10px + 2vmin);
  color: white;
}

.content {
  position: relative;
  background-color: #282c34;
  font-size: calc(10px + 2vmin);
  color: white;
  min-height: 300px;
  z-index: 0;
}

.App-link {
  color: #61dafb;
}

.overlay {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  z-index: 1;
  background-color: rgba(0, 0, 0, .8);
}

.result {
  position: absolute;
  width: 500px;
  left: 0;
  right: 0;
  margin: auto;
  -webkit-transition: all 1s;
  transition: all 1s;
  bottom: 0;
  opacity: 1;
  display: block;
  z-index: 2;
}

.hidden {
  bottom: 300px;
  opacity: 0;
}

.content-header, .content-body {
  padding: 100px 0 0;
  z-index: 0;
}

button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: none;
  width: 200px;
  height: 60px;
  background-color: rgb(4, 120, 187);
  color: #fff;
  border: none;
  border-radius: 8px;
  font-size: 18px;
  cursor: pointer;
  -webkit-transition: all .6s;
  transition: all .6s;
  z-index: 0;
  margin-top: 40px;
  opacity: .8;
}

button:hover {
  margin-top: 35px;
  opacity: 1;
}
